@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --grad-1: rgb(81,70,193);
  --grad-2: rgb(188,139,93);
}

html, body, #root {
  height:100%;
  min-height:100%; 
  overflow-x: clip;
  
}

body {
  color: #d1d1d1;
  background: #1d1d20;
  font-family: "Montserrat", sans-serif;
}

@keyframes gradient {
	0% {
		background-size: 500% 500%; 
    background-position: 100% 100%;
	}
	100% {
		background-size: 100% 100%;
    background-position: 0% 0%;
	}
}

.lightmode {
  background: radial-gradient(at bottom left,rgba(74,222,128,1) 0%,rgba(59,130,246,1) 50%, rgb(22, 63, 129) 100%);
  
}

.darkmode {
  background: radial-gradient(at bottom left,rgb(32, 124, 66) 0%,rgb(33, 73, 136) 50%, rgb(5, 29, 66) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.gradient1 {
  stop-color: var(--grad-1);
  transition: all .3s ease-in-out;
}

.gradient2 {
  stop-color: var(--grad-2);
  transition: all .3s ease-in-out;
}

.main {
  box-sizing: border-box;
  display: block;
  /* position: fixed; */
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y:visible;
  margin-bottom: 2em;


  height: 115vh;
  min-height:100%; 
  min-width: 100%;
  max-width: 100vw;
  background-size: 500% 500%;
  animation-name: gradient;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-fill-mode: forwards;
  overflow-x: clip;
}

.sections_container {
  max-width: 100vw;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  display: flex;
  word-wrap: break-word;
  overflow-x: clip;
  flex-direction: column;
}


.title {
  opacity: 0;
  animation: fadeIn 1s 1s;
  animation-fill-mode: forwards;
}

.subtitle {
  opacity: 0;
  animation: fadeIn 1s 2s;
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@property --wavePos {
  syntax: '<percentage>';
  initial-value: 60%;
  inherits: false;
}

@keyframes animateWave {
  0% {--wavePos: 0%}
  100% {--wavePos: 30%}
}

.wave {
  bottom: -16vh;
  --size: 3vw;
  --m: 0.8;
  --p: calc(var(--m)*var(--size));
  --R: calc(var(--size)*sqrt(var(--m)*var(--m) + 1));
  
  /* height: 150px; */
  height: 15vh;
  margin: 0px 0;
  align-self: self-end;

  animation-timing-function: linear;
  animation-name: animateWave;
  animation-timeline: scroll();
  animation-fill-mode: both;
  
  mask:
    radial-gradient(var(--R) at left 50% var(--d,top) calc(var(--size) + var(--p)),#000 99%,#0000 101%) 
      calc(var(--wavePos) - 2*var(--size)) 0/calc(4*var(--size)) 100%,
    radial-gradient(var(--R) at left 50% var(--d,top) calc(-1*var(--p)),#0000 99%,#000 101%) 
      left var(--wavePos) var(--d,top) var(--size)/calc(4*var(--size)) 100% repeat-x;
  background:#1d1d20;;
}
